const components = [															// 
	require('./Container').default,												// 
	require('./sandwich').default,												// 
	require('./sizeListen').default,											// 

	require('./card').default,													// 
];

const install = function (Vue) {												//  install  Vue 
	
	if (install.installed) return												// 
	install.installed = true
	
	components.map(component => {												// 
		// Vue.component(component.name, component);
		component.install(Vue);
	});
}

if (typeof window !== 'undefined' && window.Vue) {								//  Vue  Vue 
	install(window.Vue)
}

export default {
	install,																	//  install Vue.use()
	...components
}